<script>


  import request from "@/utils/request";
  import storage from "store";

  export default {
    name: "MainContent",
    data() {
      return {
        formContactUs: {
          name: '',
          contactMethod: '',
          theme: '',
          content: ''
        }
      }
    },
    methods: {
      contactUs() {
        const {name, contactMethod, theme, content} = this.formContactUs;
        let flag = true;
        if (!name) {
          flag = false;
          this.$message.error('姓名字段不能为空!');
        }
        if (!contactMethod) {
          flag = false;
          this.$message.error('联系方式字段不能为空!');
        }
        if (!theme) {
          flag = false;
          this.$message.error('主题字段不能为空!');
        }
        if (!content) {
          flag = false;
          this.$message.error('内容字段不能为空!');
        }
        if (name.length > 20) {
          flag = false;
          this.$message.error('姓名字段过长!');
        }
        if (contactMethod.length > 40) {
          flag = false;
          this.$message.error('联系方式字段过长！');
        }
        if (theme.length > 50) {
          flag = false;
          this.$message.error('主题字段过长！');
        }
        if (content.length > 1000) {
          flag = false;
          this.$message.error('内容字段过长！');
        }

        if (flag) {
          const isLogin = storage.get('token');
          if (!isLogin) {this.$message.error('请先登录！'); return;}
          request({
            method: 'post',
            url: '/law/contactUs',
            data: this.formContactUs
          }).then(res => {
            if (res.code === 1) {
              this.$message.success('提交成功！');
              this.formContactUs = {
                name: '', contactMethod: '', theme: '', content: ''
              }
            } else {
              this.$message.error(res.msg);
            }
          }).catch(error => {
            this.$message.error('服务器错误！');
            console.log(error);
          })
        }
      }
    },
    activated() {
      this.formContactUs = {
        name: '', contactMethod: '', theme: '', content: ''
      }
    }
  }
</script>


