//用于创建整个应用的路由器.
import Vue from 'vue';
import VueRouter from "vue-router";
import Home from "@/pages/Home";
import storage from "store";
//引入页面

//捕捉异常
const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter);

//创建并暴露路由器
const router = new VueRouter({
    // mode: 'history',
    routes: [
        //主页路由
        {
            name: 'home',
            path: '/',
            component: Home,
            meta: {
                index: 0,
                title: '法"智"援乡，法律援助'
            }
        },
        //高校风采
        {
            name: 'university',
            path: '/university',
            component: () => import("@/pages/university/University"),
            meta: {
                index: 1,
                title: '高校风采'
            },
            children: [
                //文章列表
                {
                    name: 'passageListUniversity',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListUniversity"),
                    meta: {
                        index: 2,
                        title: '高校风采'
                    }
                },
                //浙江工业大学
                {
                    name: 'zjut',
                    path: 'zjut',
                    component: () => import("@/pages/university/Zjut"),
                    meta: {
                        index: 3,
                        title: '高校风采'
                    }
                }
            ]
        },
        //乡镇风采
        {
            name: 'township',
            path: '/township',
            component: () => import("@/pages/township/TownShip"),
            meta: {
                index: 1,
                title: '乡镇风采'
            },
            children: [
                //文章列表
                {
                    name: 'passageListTownShip',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListTownShip"),
                    meta: {
                        index: 2,
                        title: '乡镇风采'
                    }
                },
                //江西省上饶市鄱阳县双港乡
                {
                    name: 'tsg',
                    path: 'tsg',
                    component: () => import("@/pages/township/Tsg"),
                    meta: {
                        index: 3,
                        title: '乡镇风采'
                    }
                },
                //饶市鄱阳县莲湖乡
                {
                    name: 'tlh',
                    path: 'tlh',
                    component: () => import("@/pages/township/Tlh"),
                    meta: {
                        index: 3,
                        title: '乡镇风采'
                    }
                },
                //浙江省丽水市青田县鹤城
                {
                    name: 'thc',
                    path: 'thc',
                    component: () => import("@/pages/township/Thc"),
                    meta: {
                        index: 3,
                        title: '乡镇风采'
                    }
                }
            ]

        },
        //团队资讯
        {
            name: 'team',
            path: '/team',
            component: () => import("@/pages/team/Team"),
            meta: {
                index: 1,
                title: '团队资讯'
            },
            children: [
                //文章列表
                {
                    name: 'passageListTeam',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListTeam"),
                    meta: {
                        index: 2,
                        title: '团队资讯'
                    }
                },
                //下姜村
                {
                    name: 'xjc',
                    path: 'xjc',
                    component: () => import("@/pages/team/Xjc"),
                    meta: {
                        index: 3,
                        title: '团队资讯'
                    }
                }

            ]
        },
        //律所风采
        {
            name: 'lawFirm',
            path: '/lawFirm',
            component: () => import("@/pages/lawFirm/LawFirm"),
            meta: {
                index: 1,
                title: '律所风采'
            },
            children: [
                //文章列表
                {
                    name: 'passageListLawFirm',
                    path: '',
                    component: () => import("@/pages/passageList/PassageListLawFirm"),
                    meta: {
                        index: 2,
                        title: '律所风采'
                    }
                },
                //上海靖之霖（杭州）律师事务所
                {
                    name: 'jzl',
                    path: 'jzl',
                    component: () => import("@/pages/lawFirm/Jzl"),
                    meta: {
                        index: 3
                    }
                },
                //浙江泽鼎律师事务所
                {
                    name: 'zd',
                    path: 'zd',
                    component: () => import("@/pages/lawFirm/Zd"),
                    meta: {
                        index: 3
                    }
                }
            ]
        },
        //法律工具
        {
            name: 'legalTools',
            path: '/legalTools',
            component: () => import("@/pages/legalTools/LegalTools"),
            meta: {
                index: 1,
                title: '法律工具'
            },
            children: [
                //常见问题
                {
                    name: 'faq',
                    path: 'faq',
                    component: () => import("@/pages/legalTools/Faq"),
                    meta: {
                        index: 2,
                        title: '常见问题'
                    }
                },
                //文书模板
                {
                    name: 'instrumentTemplate',
                    path: 'instrumentTemplate',
                    component: () => import("@/pages/legalTools/InstrumentTemplate"),
                    meta: {
                        index: 2,
                        title: '文书模板'
                    }
                },
                //赔偿计算
                {
                    name: 'compensationCalculation',
                    path: 'compensationCalculation',
                    component: () => import("@/pages/legalTools/CompensationCalculation"),
                    meta: {
                        index: 2,
                        title: '赔偿计算'
                    }
                }
            ]
        },
        //登录页面
        {
            name: 'login',
            path: '/login',
            redirect: {name: 'loginAccount'},
            component: () => import("@/pages/login/Login"),
            meta: {
                index: 1,
                title: '登录'
            },
            children: [
                //账号密码登录
                {
                    name: 'loginAccount',
                    path: '',
                    component: () => import("@/pages/login/loginForm/LoginAccount"),
                    meta: {
                        title: '登录'
                    },
                    beforeEnter: (to, from, next)=>{
                        if (!!storage.get('token')){
                            //如果已经登录，跳转到主页
                            alert('您已经登录！');
                            next({name: 'home'});
                        }else {
                            next();
                        }
                    }
                },
                //手机号登录
                {
                    name: 'loginPhone',
                    path: 'phone',
                    component: () => import("@/pages/login/loginForm/LoginPhone"),
                    meta: {
                        title: '登录'
                    },
                    beforeEnter: (to, from, next)=>{
                        if (!!storage.get('token')){
                            //如果已经登录，跳转到主页
                            alert('您已经登录！');
                            next({name: 'home'});
                        }else {
                            next();
                        }
                    }
                },
            ]
        },
        //用户注册页面
        {
            name: 'register',
            path: '/register',
            component: () => import("@/pages/login/loginForm/Register"),
            meta: {
                index: 2,
                title: '用户注册'
            },
            beforeEnter: (to, from, next)=>{
                if (!!storage.get('token')){
                    //如果已经登录，跳转到主页
                    alert('您已经登录！');
                    next({name: 'home'});
                }else {
                    next();
                }
            }
        },
        //修改/找回密码
        {
            name: 'getPass',
            path: '/getPass',
            component: () => import("@/pages/login/loginForm/ForgotPassword"),
            meta: {
                index: 2,
                title: '找回密码'
            }
        },
        //法律咨询
        {
            name: 'legalAdvice',
            path: '/legalAdvice',
            component: () => import("@/pages/legalAdvice/LegalAdvice"),
            meta: {
                index: 1,
                title: '法律咨询',
                isAuth: true
            },
            children: [
                //日常法律咨询/发起咨询
                {
                    name: 'dailyConsultation',
                    path: 'dailyConsultation',
                    component: () => import("@/pages/legalAdvice/legalAdviceForm/daily/ConductConsultation"),
                    meta: {
                        isAuth: true
                    }
                },
                //日常法律咨询/我的咨询(列表)
                {
                    name: 'myConsultation',
                    path: 'myConsultation',
                    component: () => import("@/pages/legalAdvice/legalAdviceForm/daily/MyConsultation"),
                    meta: {
                        isAuth: true
                    }
                },
                //日常法律咨询/我的咨询(详情)
                {
                    name: 'consultationDetail',
                    path: 'detail',
                    component: () => import("@/pages/legalAdvice/legalAdviceForm/daily/ConsultationDetail"),
                    meta: {
                        isAuth: true
                    }
                },
                //乡村创业法律咨询/发起咨询
                {
                    name: 'consultation',
                    path: 'consultation',
                    component: () => import("@/pages/legalAdvice/legalAdviceForm/daily/ConductConsultation"),
                    meta: {
                        isAuth: true
                    }
                },
                //乡村创业法律咨询/我的咨询(列表)
                {
                    name: 'myConsultation',
                    path: 'myConsultation',
                    component: () => import("@/pages/legalAdvice/legalAdviceForm/daily/MyConsultation"),
                    meta: {
                        isAuth: true
                    }
                }
            ]
        },
        //修改密码成功页面
        {
            name: 'changePwdSuccess',
            path: '/changePwdSuccess',
            component: () => import("@/pages/login/loginForm/ChangePwdSuccess"),
            meta: {
                index: 1,
                title: '修改密码成功页面'
            },
            beforeEnter: (to, from, next) => {
                if (from.name !== 'getPass') {
                    alert('无权查看该页面!');
                    next({name: 'home'});
                }
                next();
            }
        },
        //注册成功页面
        {
            name: 'registerSuccess',
            path: '/registerSuccess',
            component: () => import("@/pages/login/loginForm/RegisterSuccess"),
            meta: {
                index: 1,
                title: '注册成功'
            },
            beforeEnter: (to, from, next) => {
                if (from.name !== 'register') {
                    alert('无权查看该页面!');
                    next({name: 'home'});
                }
                next();
            }
        },
        //个人中心页面
        {
            name: 'userCenter',
            path: '/userCenter',
            component: () => import("@/pages/user/UserCenter"),
            meta: {
                index: 1,
                title: '个人中心',
                isAuth: true
            }
        },
        //修改手机号页面
        {
            name: 'changePhone',
            path: '/changePhone',
            component: ()=>import("@/pages/user/changePhone"),
            meta:{
                index: 1,
                title: '修改手机号',
                isAuth: true
            }
        },
        //修改手机号成功页面
        {
            name: 'changePhoneSuccess',
            path: '/changePhoneSuccess',
            component: ()=>import("@/pages/user/ChangePhoneSuccess"),
            meta: {
                index: 2,
                title: '修改手机号成功'
            },
            beforeEnter: (to, from, next) => {
                if (from.name !== 'changePhone') {
                    alert('无权查看该页面!');
                    next({name: 'home'});
                }
                next();
            }
        }
    ],
})


router.beforeEach((to, from, next) => {
    
    if (to.meta.title) {
        document.title = to.meta.title;
    } else {
        document.title = '法"智"援乡，法律援助';
    }
    
    if (to.meta.isAuth){
        //只有登录了，才能查看需要鉴权的页面
        if (!!storage.get('token')){
            next();
        }else {
            alert('您还没有登录！请先登录！');
            next({name: 'loginAccount'});
        }
    }
    
    next();
})


export default router