import Vue from 'vue';
import App from './App.vue';
import 'bootstrap/dist/css/bootstrap.css'; //引用bootstrap的样式
import 'bootstrap/dist/js/bootstrap.min.js'; //引用bootstrap的js
import '@/assets/css/variables-green.css';
import '@/assets/css/general.css';
import '@/assets/js/general';

//vendor
import "@/assets/vendor/bootstrap-icons/bootstrap-icons.css";
//Vue Router
import VueRouter from "vue-router";
import router from "@/router";
//aos
import AOS from 'aos';
import 'aos/dist/aos.css';
//vue scrollTo
import VueScrollTo from 'vue-scrollto';
//Antd
import {
    Alert,
    Avatar,
    Button,
    Cascader,
    Checkbox,
    Comment,
    Descriptions,
    Divider,
    Form,
    FormModel,
    Icon,
    Input,
    List,
    Menu,
    Radio,
    Select,
    Switch,
    Tooltip,
    Upload,
    message,
    Result,
    Dropdown,
    Spin,
    Popconfirm,
    Modal
} from 'ant-design-vue';
import confirm from "ant-design-vue/lib/modal/confirm";

Vue.config.productionTip = false;

//使用插件
Vue.use(VueRouter);
Vue.use(FormModel);
Vue.use(Button);
Vue.use(Icon);
Vue.use(Alert);
Vue.use(Input);
Vue.use(Checkbox);
Vue.use(Menu);
Vue.use(Radio);
Vue.use(Switch);
Vue.use(Select);
Vue.use(Cascader);
Vue.use(Upload);
Vue.use(List);
Vue.use(Avatar);
Vue.use(Descriptions);
Vue.use(Tooltip);
Vue.use(Comment);
Vue.use(Divider);
Vue.use(Form);
Vue.use(Result);
Vue.use(Dropdown);
Vue.use(Spin);
Vue.use(Popconfirm);
Vue.use(Modal);

let scrollOptions = {
    container: "body", //滚动的容器
    duration: 500, //滚动时间
    easing: "ease", //缓动类型
    offset: -50, // 滚动时应应用的偏移量。此选项接受回调函数
    force: true, // 是否应执行滚动
    cancelable: true, // 用户是否可以取消滚动
    onStart: false, // 滚动开始时的钩子函数
    onDone: false, // 滚动结束时候的钩子函数
    onCancel: false, // 用户取消滚动的钩子函数
    x: false, // 是否要在x轴上也滚动
    y: true    // 是否要在y轴上滚动
};

Vue.use(VueScrollTo, scrollOptions);
Vue.prototype.$message = message;
Vue.prototype.$info = Modal.info;
Vue.prototype.$success = Modal.success;
Vue.prototype.$error = Modal.error;
Vue.prototype.$warning = Modal.warning;
Vue.prototype.$confirm = Modal.confirm;
Vue.prototype.$destroyAll = Modal.destroyAll;

new Vue({
    render: h => h(App),
    router: router,
    beforeCreate() {
        Vue.prototype.$bus = this;
    },
    mounted() {
        AOS.init();
    }
}).$mount('#app')
