<template>
  <!--Header组件-->
  <header id="header" class="header fixed-top" data-scrollto-offset="0">
    <div class="container-fluid d-flex align-items-center justify-content-between">

      <a @click.prevent="goAnchor('#home-page01', 'home')" href=""
         class="logo d-flex align-items-center scrollto me-auto me-lg-0">
        <img src="~@/assets/img/logo.png" alt="logo">
        <h1>律法青苗公益服务组织<span>.</span></h1>
      </a>

      <nav id="navbar" class="navbar">
        <ul>
          <li><a @click.prevent="goAnchor('#home-page01', 'home')" class="nav-link" href="">主页</a></li>
          <li>
            <router-link :to="{name: 'legalAdvice'}" class="nav-link">法律咨询</router-link>
          </li>
          <li>
            <router-link :to="{name: 'legalTools'}" class="nav-link">法律工具</router-link>
          </li>
          <li>
            <router-link class="nav-link" :to="{name: 'passageListUniversity'}">高校风采</router-link>
          </li>
          <li>
            <router-link class="nav-link" :to="{name: 'passageListTownShip'}">乡镇风采</router-link>
          </li>
          <li>
            <router-link class="nav-link" :to="{name: 'passageListLawFirm'}">律所风采</router-link>
          </li>
          <li>
            <router-link :to="{name: 'passageListTeam'}" class="nav-link">团队资讯</router-link>
          </li>
          <li><a class="nav-link" @click.prevent="goAnchor('#contact','home')" href="">联系我们</a></li>
        </ul>
        <i class="bi bi-list mobile-nav-toggle d-none"></i>
      </nav><!-- .navbar -->


      <div v-if="!isLogin">
        <button @click="$router.push({name: 'loginAccount'});" class="btn btn-getstarted">登录 / 注册</button>
      </div>
      <div v-else>
        <a-dropdown>
          <a class="ant-dropdown-link" @click="e => e.preventDefault()">
            <a-avatar icon="user"/>
            <span class="user-info"> &nbsp;{{ userInfo ? userInfo.username : '' }} &nbsp;</span>
          </a>
          <a-menu slot="overlay" @click="handleMenuClick">
            <a-menu-item key="1">
              <a-icon type="user"/>
              <router-link :to="{name: 'userCenter'}">个人中心</router-link>
            </a-menu-item>
            <a-menu-item key="2">
              <a-icon type="question-circle"/>
              <router-link :to="{name: 'myConsultation'}">我的咨询</router-link>
            </a-menu-item>
            <a-menu-item key="3">
              <a-icon type="logout"/>
              <a href="javascript:;" @click.prevent="logOut">退出登录</a>
            </a-menu-item>
          </a-menu>
        </a-dropdown>
      </div>

    </div>
  </header>
  <!-- End Header -->

</template>

<script>
  import storage from "store";
  import request from "@/utils/request";

  export default {
    name: "Header",
    // props: ['isLogin', 'userInfo'],
    data() {
      return {
        isLogin: !!storage.get('token'),
        userInfo: {
          uid: '',
          username: '',
          phone: ''
        },
      }
    },
    methods: {
      goAnchor(selector, to) {
        //参数selector是id选择器,to是跳转的路由名称
        if (this.$route.name === to) {
          //已经在当前页面，滚动到选择器位置
          document.querySelector(selector).scrollIntoView({
            behavior: 'smooth'
          })
        } else {
          //不在主页，需要进行跳转
          this.$router.push({
            name: to
          })
          this.$nextTick(function () {
            setTimeout(() => {
              document.querySelector(selector).scrollIntoView({
                behavior: 'smooth'
              })
            }, 1000)
          })
        }
      },
      getUserInfo() {
        this.userInfo = storage.get('userInfo');
        if (this.userInfo) {
          return;
        }

        request({
          method: 'get',
          url: '/ouser'
        }).then(res => {
          if (res.code === 1) {
            const userInfo = res.data.map;
            this.userInfo = userInfo;
            storage.set('userInfo', userInfo);
          } else {
            this.$message.error(res.msg);
          }
        }).catch(error => {
          this.$message.error('服务器错误！');
          console.log(error);
        })
      },
      handleMenuClick(e) {
        const key = e.key;
        // console.log(key);
      },
      logOut() {
        storage.clearAll();
        // this.$bus.$emit('setLoginStatus', false)
        this.isLogin = false;
        this.$router.push({name: 'home'});
      }
    },
    activated() {
      this.isLogin = !!storage.get('token');
      if (this.isLogin) {
        if (!!storage.get('userInfo') === false) {
          this.getUserInfo();
        } else {
          this.userInfo = storage.get('userInfo');
        }
      }
    },
  }
</script>

<style scoped>

  /*--------------------------------------------------------------
  # Header
  --------------------------------------------------------------*/
  .header {
    padding: 15px 0;
    transition: all 0.5s;
    background: var(--color-white);
    box-shadow: 0px 2px 20px rgba(var(--color-secondary-rgb), 0.1);
    z-index: 997;
  }

  .header.sticked {
    opacity: 70%;
  }

  .header .logo img {
    max-height: 40px;
    margin-right: 6px;
    margin-bottom: 5px;
  }

  .header .logo h1 {
    font-size: 32px;
    font-weight: 300;
    color: var(--color-secondary);
    font-family: var(--font-secondary);
  }

  .header .logo h1 span {
    color: var(--color-primary);
    font-weight: 500;
  }

  .header .btn-getstarted,
  .header .btn-getstarted:focus {
    font-size: 16px;
    color: var(--color-white);
    background: var(--color-primary);
    padding: 8px 23px;
    border-radius: 4px;
    transition: 0.4s;
    font-family: var(--font-secondary);
    margin-right: 10px;
  }

  .header .btn-getstarted:hover,
  .header .btn-getstarted:focus:hover {
    color: var(--color-white);
    background: rgba(var(--color-primary-rgb), 0.85);
  }

  @media (max-width: 1279px) {

    .header .btn-getstarted,
    .header .btn-getstarted:focus {
      margin-right: 20px;
    }
  }

  /*--------------------------------------------------------------
  # Desktop Navigation
  --------------------------------------------------------------*/
  @media (min-width: 1280px) {
    .navbar {
      padding: 0;
      position: relative;
    }

    .navbar ul {
      margin: 0;
      padding: 0;
      display: flex;
      list-style: none;
      align-items: center;
    }

    .navbar li {
      position: relative;
    }

    .navbar > ul > li {
      white-space: nowrap;
    }

    .navbar a,
    .navbar a:focus {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 14px 20px;
      font-family: var(--font-secondary);
      font-size: 16px;
      font-weight: 400;
      color: rgba(var(--color-default), 0.7);
      white-space: nowrap;
      transition: 0.3s;
      position: relative;
    }

    .navbar a i,
    .navbar a:focus i {
      font-size: 12px;
      line-height: 0;
      margin-left: 5px;
    }

    .navbar > ul > li > a:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: 0;
      left: 0;
      background-color: var(--color-primary);
      visibility: hidden;
      transition: all 0.3s ease-in-out 0s;
      transform: scaleX(0);
      transition: all 0.3s ease-in-out 0s;
    }

    .navbar a:hover:before,
    .navbar li:hover > a:before,
    .navbar .active:before {
      visibility: visible;
      transform: scaleX(0.7);
    }

    .navbar a:hover,
    .navbar .active,
    .navbar .active:focus,
    .navbar li:hover > a {
      color: var(--color-primary);
    }

    .navbar .dropdown a:hover:before,
    .navbar .dropdown:hover > a:before,
    .navbar .dropdown .active:before {
      visibility: hidden;
    }

    .navbar .dropdown a:hover,
    .navbar .dropdown .active,
    .navbar .dropdown .active:focus,
    .navbar .dropdown:hover > a {
      color: var(--color-white);
      background: var(--color-secondary);
    }

    .navbar .dropdown ul {
      display: block;
      position: absolute;
      left: 0;
      top: 100%;
      margin: 0;
      padding: 0 0 10px 0;
      z-index: 99;
      opacity: 0;
      visibility: hidden;
      background: var(--color-secondary);
      transition: 0.3s;
    }

    .navbar .dropdown ul li {
      min-width: 200px;
    }

    .navbar .dropdown ul a {
      padding: 10px 20px;
      font-size: 15px;
      text-transform: none;
      font-weight: 400;
      color: rgba(var(--color-white-rgb), 0.5);
    }

    .navbar .dropdown ul a i {
      font-size: 12px;
    }

    .navbar .dropdown ul a:hover,
    .navbar .dropdown ul .active,
    .navbar .dropdown ul .active:hover,
    .navbar .dropdown ul li:hover > a {
      color: var(--color-white);
      background: var(--color-primary);
    }

    .navbar .dropdown:hover > ul {
      opacity: 1;
      visibility: visible;
    }

    .navbar .megamenu {
      position: static;
    }

    .navbar .megamenu ul {
      right: 0;
      padding: 10px;
      display: flex;
    }

    .navbar .megamenu ul li {
      flex: 1;
    }

    .navbar .megamenu ul li a,
    .navbar .megamenu ul li:hover > a {
      color: rgba(var(--color-white-rgb), 0.5);
      background: none;
    }

    .navbar .megamenu ul li a:hover,
    .navbar .megamenu ul li .active,
    .navbar .megamenu ul li .active:hover {
      color: var(--color-white);
      background: var(--color-primary);
    }

    .navbar .dropdown .dropdown ul {
      top: 0;
      left: calc(100% - 30px);
      visibility: hidden;
    }

    .navbar .dropdown .dropdown:hover > ul {
      opacity: 1;
      top: 0;
      left: 100%;
      visibility: visible;
    }
  }

  @media (min-width: 1280px) and (max-width: 1366px) {
    .navbar .dropdown .dropdown ul {
      left: -90%;
    }

    .navbar .dropdown .dropdown:hover > ul {
      left: -100%;
    }
  }

  /*--------------------------------------------------------------
  # Mobile Navigation
  --------------------------------------------------------------*/
  @media (max-width: 1279px) {
    .navbar {
      position: fixed;
      top: 0;
      left: -100%;
      width: calc(100% - 70px);
      bottom: 0;
      transition: 0.3s;
      z-index: 9997;
    }

    .navbar ul {
      position: absolute;
      inset: 0;
      padding: 10px 0;
      margin: 0;
      background: rgba(var(--color-secondary-rgb), 0.9);
      overflow-y: auto;
      transition: 0.3s;
      z-index: 9998;
    }

    .navbar a,
    .navbar a:focus {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 20px;
      font-size: 16px;
      font-weight: 500;
      color: rgba(var(--color-white-rgb), 0.7);
      white-space: nowrap;
      transition: 0.3s;
    }

    .navbar a i,
    .navbar a:focus i {
      font-size: 12px;
      line-height: 0;
      margin-left: 5px;
    }

    .navbar a:hover,
    .navbar .active,
    .navbar .active:focus,
    .navbar li:hover > a {
      color: var(--color-white);
    }

    .navbar .dropdown ul,
    .navbar .dropdown .dropdown ul {
      position: static;
      display: none;
      padding: 10px 0;
      margin: 10px 20px;
      transition: all 0.5s ease-in-out;
      border: 1px solid rgba(var(--color-secondary-light-rgb), 0.3);
    }

    .navbar .dropdown > .dropdown-active,
    .navbar .dropdown .dropdown > .dropdown-active {
      display: block;
    }

    .mobile-nav-toggle {
      display: block !important;
      color: var(--color-secondary);
      font-size: 28px;
      cursor: pointer;
      line-height: 0;
      transition: 0.5s;
      position: fixed;
      top: 20px;
      z-index: 9999;
      right: 20px;
    }

    .mobile-nav-toggle.bi-x {
      color: var(--color-white);
    }

    .mobile-nav-active {
      overflow: hidden;
      z-index: 9995;
      position: relative;
    }

    .mobile-nav-active .navbar {
      left: 0;
    }

    .mobile-nav-active .navbar:before {
      content: "";
      position: fixed;
      inset: 0;
      background: rgba(var(--color-secondary-rgb), 0.8);
      z-index: 9996;
    }
  }

  .user-info {
    color: rgba(0, 0, 0, .65);
    font-size: 16px;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
    font-variant: tabular-nums;
  }

  .ant-dropdown-menu-item > a {
    display: inline-block;
  }
</style>