<template>
  <!-- ======= Footer ======= -->
  <div>
    <footer id="footer" class="footer">
      <div class="footer-legal text-center">
        <div class="container d-flex flex-column">
          <div>
            <div class="copyright text-center">
              &copy; Copyright <strong><span>XXXXX</span></strong>. All Rights Reserved
            </div>
            <!--<div class="credits">
              &lt;!&ndash; All the links in the footer should remain intact. &ndash;&gt;
              &lt;!&ndash; You can delete the links only if you purchased the pro version. &ndash;&gt;
              &lt;!&ndash; Licensing information: https://bootstrapmade.com/license/ &ndash;&gt;
              &lt;!&ndash; Purchase the pro version with working PHP/AJAX contact form: https://bootstrapmade.com/herobiz-bootstrap-business-template/ &ndash;&gt;
              Designed by <a href="https://bootstrapmade.com/">BootstrapMade</a>
            </div>-->
          </div>
        </div>
      </div>
    </footer>
    <!-- End Footer -->
    <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeIn"
        leave-active-class="animate__fadeOut"
    >
      <a v-if="gotop" @click.prevent="toTop" href=""
         class="scroll-top d-flex align-items-center justify-content-center"><i class="bi bi-arrow-up-short"></i></a>
    </transition>
  </div>

</template>

<script>
  import 'animate.css'

  export default {
    name: 'Footer',
    data() {
      return {
        gotop: false,
      }
    },
    methods: {
      handleScroll() {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        scrollTop > 600 ? (this.gotop = true) : (this.gotop = false);
      },
      toTop() {
        let top = document.body.scrollTop;
        // 实现滚动效果
        const timeTop = setInterval(() => {
          document.body.scrollTop = document.documentElement.scrollTop = top -= 50;
          if (top <= 0) {
            clearInterval(timeTop);
          }
        }, 10);
      }
    },
    mounted() {
      // 此处true需要加上，不加滚动事件可能绑定不成功
      window.addEventListener("scroll", this.handleScroll, true);
    },
  }
</script>

